@import "./../../assets/css/variable";
@import "./../../assets/css/mixins";

.header {
  background: #070720;
}

.header__logo {
  padding: 20px 0 17px;

  a {
    display: inline-block;
  }
}

.header__menu {
  text-align: center;

  ul {
    li {
      list-style: none;
      display: inline-block;
      position: relative;
      margin-right: 16px;

      &.active {
        a {
          background: $primary-color;
          color: $white-color;
        }
      }

      &:hover {
        a {
          // background: $primary-color;
          color: $white-color;
        }

        .dropdown {
          top: 62px;
          opacity: 1;
          visibility: visible;

          li {
            a {
              background: transparent;
            }
          }
        }
      }

      &:last-child {
        margin-right: 0;
      }

      .dropdown {
        position: absolute;
        left: 0;
        top: 82px;
        width: 150px;
        background: $white-color;
        text-align: left;
        padding: 5px 0;
        z-index: 9;
        opacity: 0;
        visibility: hidden;
        z-index: 999999;
        @include transition(all, 0.3s);

        li {
          display: block;
          margin-right: 0;

          a {
            font-size: 14px;
            color: $heading-color;
            font-weight: 500;
            padding: 5px 20px;
          }
        }
      }

      a {
        font-size: 15px;
        color: #b7b7b7;
        display: block;
        font-weight: 700;
        @include transition(all, 0.5s);
        padding: 20px;

        span {
          position: relative;
          font-size: 17px;
          top: 2px;
        }
      }
    }
  }
}

.header__right {
  text-align: right;
  padding: 20px 0 15px;

  a {
    display: inline-block;
    font-size: 18px;
    color: $white-color;
    margin-right: 30px;

    &:last-child {
      margin-right: 0;
    }
  }
}

.slicknav_menu {
  display: none;
}
